import Vue from 'vue'
import App from './App.vue'
import router from './router/index';
import store from './store/index';
import VToolTip from 'v-tooltip';
import VeeValidate from 'vee-validate';

Vue.config.productionTip = false;

Vue.use(VToolTip);
Vue.use(VeeValidate, {
  events: 'blur'
});


new Vue({
  render: h => h(App),
  router,
  store
}).$mount('#app')
