<template>
    <div class="tasks-content-wrapper">
        <div class="tasks-list-wrapper">
            <div class="tasks-list"
                 ref="tasksList"
                 v-if="tasks.length"
            >
                <div
                     :class="taskClass(task)"
                     v-for="task in tasks"
                     :key="task.id"
                     :data-task-id="task.id"
                >
                    <div class="tasks-list-item-top-line">
                        <div class="tasks-list-item__info">
                            <div class="tasks-list-item__title">
                                {{ task.title }}
                            </div>
                            <div class="tasks-list-item__delete"
                                 v-tooltip.right="{content: 'Удалить'}"
                                 @click="showDeleteDialog(task.id)"
                            >
                                <i class="material-icons">remove_circle</i>
                            </div>
                            <div :class="taskStatusClass(task)"
                                 v-tooltip.top-center="{content: 'Изменить статус'}"
                                 @click="toggleStatus(task.id)"
                            >
                                {{  taskStatusText(task) }}
                            </div>
                        </div>
                    </div>
                    <div class="tasks-list-item__body">
                        {{ task.body  }}
                    </div>
                </div>
            </div>
            <div class="tasks-list-empty"
                 v-else
            >
                <p>
                    На данный момент у вас не имеется заданий
                </p>
                <button class="tasks-list-empty__add-btn"
                        v-tooltip.bottom="{content: 'Добавить Задачу'}"
                        @click="toggleCreateForm()"
                >
                    Добавить
                </button>
            </div>
        </div>
    </div>
</template>

<script>
    import {mapGetters,mapMutations} from 'vuex';
    import {TASKS_MODULE, tasksMutationsConstants} from "../../../constants/tasksConstants";
    import {APP_MODULE, appMutationsConstants} from "../../../constants/applicationConstants";

export default {
  name: 'task-list',
  computed: {
      ...mapGetters({
          tasks: `${TASKS_MODULE}/tasks`,
          activeTaskId: `${TASKS_MODULE}/activeTaskId`,
          findActiveIndex: `${TASKS_MODULE}/findActiveIndex`
      })
  },
  watch: {
     'activeTaskId' (newVal,oldVal) {
         if(oldVal != newVal){
             const {tasksList} = this.$refs;
             const screenWidth = document.documentElement.clientWidth;

             if(screenWidth <= 1024)
                this.$nextTick(() => {
                    let domElement = document.querySelector(`[data-task-id="${this.activeTaskId}"]`);
                    if(domElement){
                        window.scrollTo({
                            top: domElement.offsetTop,
                            behavior: "smooth"
                        });
                    };
                })
             else{
                 if(tasksList) {
                     this.$nextTick(() => {
                         const index = this.findActiveIndex(newVal);
                         const screenHeight = document.documentElement.clientHeight;
                         const marginTop = index * screenHeight;
                         tasksList.style.marginTop = `-${marginTop}px`;
                     })
                 }
             }
         }
     }
  },
  methods: {
      ...mapMutations({
         toggleStatus: `${TASKS_MODULE}/${tasksMutationsConstants.TOGGLE_STATUS}`,
         showDeleteDialog: `${TASKS_MODULE}/${tasksMutationsConstants.PUT_TASK_ID_FOR_DELETE}`,
         toggleCreateForm: `${APP_MODULE}/${appMutationsConstants.TOGGLE_CREATE_FORM}`
      }),
      taskStatusText(task)
      {
          return task.completed ? "Выполнено" : "В процессе";
      },
      taskStatusClass(task)
      {
          return {
              "tasks-list-item__status" : true,
              "tasks-list-item__status--complete" : task.completed,
          }
      },
      taskClass(task)
      {
          return {
              'tasks-list-item': true,
              'tasks-list-item--active' : task.id == this.activeTaskId
          };
      }
  }
}

</script>

