<template>
    <div class="tasks-navigation-wrapper">
        <div class="medium-menu"
             @click="toggleMediumTasksNav()"
        >
            <i class="material-icons">menu</i>
        </div>
        <div :class="tasksNavigationClass()">
            <div class="tasks-navigation-top-line">
                <h2 class="tasks-navigation-top-line__title">Меню</h2>
                <div class="tasks-navigation-top-line__create-btn"
                     v-tooltip.top-center="{content: 'Создайте Задание'}"
                     @click="toggleCreateForm()"
                >
                    <i class="material-icons">add_circle</i>
                </div>
            </div>
            <div class="tasks-navigation-list">
                <div v-for="task in tasks"
                     :key="task.id"
                     :class="taskNavigationItemClass(task.id)"
                     @click="putActiveTask(task.id)"
                >
                    {{ task.title  }}
                </div>
            </div>
        </div>
    </div>
</template>

<script>

import {TASKS_MODULE , tasksMutationsConstants} from "../../../constants/tasksConstants";
import {mapGetters, mapMutations} from 'vuex';
import {APP_MODULE, appMutationsConstants} from "../../../constants/applicationConstants";


export default {
    name: 'task-navigation',
    computed: {
        ...mapGetters({
            tasks: `${TASKS_MODULE}/tasks`,
            isActiveTask: `${TASKS_MODULE}/isActiveTask`,
            needShowMediumTasksNav: `${APP_MODULE}/needShowMediumTasksNavigation`
        }),
    },
    methods: {
        ...mapMutations({
           putActiveTask: `${TASKS_MODULE}/${tasksMutationsConstants.PUT_ACTIVE_CATEGORY}`,
           toggleCreateForm: `${APP_MODULE}/${appMutationsConstants.TOGGLE_CREATE_FORM}`,
           toggleMediumTasksNav: `${APP_MODULE}/${appMutationsConstants.TOGGLE_MEDIUM_TASKS_NAVIGATION}`
        }),
        tasksNavigationClass()
        {
            return {
                'tasks-navigation': true,
                'tasks-navigation--visible' : this.needShowMediumTasksNav
            }
        },
        taskNavigationItemClass(taskId) {
            return {
                'tasks-navigation-list__item' : true,
                'tasks-navigation-list__item--active' : this.isActiveTask(taskId)
            }
        }
    }
}
</script>

