<template>
    <div>
        <circles-spinner :isLoading="isLoading"></circles-spinner>
        <div    v-if="needShow"
                class="create-task-form-wrapper">
            <form class="create-task-form">
                <div class="create-task-form__close"
                     @click="toggleForm()"
                >
                    <i class="material-icons">close</i>
                </div>
                <h3 class="create-task-form__title">Создайте Задачу</h3>
                <div class="input-wrapper">
                    <div class="input__title">Заголовок: </div>
                    <input type="text"
                           :class="inputControlClasses('title')"
                           v-model="task.title"
                           v-validate="'required'"
                           name="title"
                    >
                    <div   v-show="errors.has('title')"
                            class="input-control__error-text">
                            {{  errors.first('title') }}
                    </div>
                </div>
                <div class="input-wrapper">
                    <div class="input__title">Описание: </div>
                    <textarea :class="inputControlClasses('body')"
                              v-model="task.body"
                              v-validate="'required'"
                              rows="8"
                              name="body"
                    ></textarea>
                    <div   v-show="errors.has('body')"
                            class="input-control__error-text">
                            {{  errors.first('body') }}
                    </div>
                </div>
                <button class="create-task-form__button"
                        @click.prevent="putTask()"
                        :disabled="isLoading || !isFormValid"
                >
                    Опубликовать
                </button>
            </form>
        </div>
    </div>
</template>

<script>
    import {mapMutations,mapGetters,mapActions} from 'vuex';
    import {TASKS_MODULE, tasksActionsContstants, tasksMutationsConstants} from "../../constants/tasksConstants";
    import {APP_MODULE, appMutationsConstants} from "../../constants/applicationConstants";
    import CirclesSpinner from "../Shared/Spinner/CirclesSpinner.vue";

export default {
  components: {CirclesSpinner},
  name: 'create-task-form',
  props: ["needShow"],
  data: () => ({
        task: {
            title: "",
            body: ""
        }
  }),
  computed: {
      ...mapGetters({
          isLoading: `${TASKS_MODULE}/isLoading`
      }),
      isFormValid()
      {
          return this.errors.items.length == 0;
      }
  },
  methods: {
      ...mapActions({
         createTaskInStore: `${TASKS_MODULE}/${tasksActionsContstants.CREATE_TASK}`
      }),
      ...mapMutations({
            toggleForm: `${APP_MODULE}/${appMutationsConstants.TOGGLE_CREATE_FORM}`
      }),
      putTask()
      {
          let task = this.makeTask();
          this.$validator.validate()
              .then(valid => {
                 if(valid){
                     this.clearTask();
                      this.createTaskInStore(task);
                  }
              })
      },
      makeTask()
      {
          return {
              title: this.task.title,
              body: this.task.body,
              completed: false
          }
      },
      clearTask() {
            this.task.title = "";
            this.task.body = "";
      },
      inputControlClasses(key)
      {
          return {
              'input-control' : true,
              'input-control--error': this.errors.has(key)
          }
      }
  }
}
</script>

