<script>
export default {
    name: 'register-form',
    data: () => ({
        needShowMoreButtons: false
    }),
    computed: {
        toggleBtnGroupClasses() {
            return {
                'register-form__toggle-btn-group': true,
                'register-form__toggle-btn-group--visible': this.needShowMoreButtons
            }
        },
        toggleHintText()
        {
            return this.needShowMoreButtons ? "Скрыть" : "Другие";
        }
    },
    methods:
    {
        toggleButtons()
        {
            this.needShowMoreButtons = !this.needShowMoreButtons;
        }
    }
}
