import {tasksMutationsConstants, tasksActionsContstants} from '../constants/tasksConstants';
import {APP_MODULE,appMutationsConstants} from '../constants/applicationConstants';

let taskId = 50;

const initialState = {
    isLoading: false,
    tasks: [
    ],
    activeTaskId : null,
    taskIdForDelete: null,
    isDeleteActionStart : false
}

export const state = Object.assign({},initialState);

export const actions = {
    [tasksActionsContstants.CREATE_TASK](context,task)
    {
        context.commit(`${tasksMutationsConstants.START_CREATE_FORM_LOADING}`);
        task.id = taskId++;

        setTimeout(() =>  {
            context.commit(`${tasksMutationsConstants.PUT_TASK}`, task)
            context.commit(`${APP_MODULE}/${appMutationsConstants.TOGGLE_CREATE_FORM}`, null, {root: true});
            context.commit(`${tasksMutationsConstants.PUT_ACTIVE_CATEGORY}`, task.id);
        }, 1000);
    },
    [tasksActionsContstants.DELETE_TASK](context)
    {
        const {taskIdForDelete} = context.state;
        if(taskIdForDelete == null) return false;
        context.commit(`${tasksMutationsConstants.DELETE_ACTION_START}`);

        setTimeout(() => {
            context.commit(`${tasksMutationsConstants.REMOVE_TASK}`, taskIdForDelete);
            context.commit(`${tasksMutationsConstants.HIDE_DELETE_DIALOG}`);
        }, 1000);

    }
};




export const mutations = {
    [tasksMutationsConstants.START_CREATE_FORM_LOADING](state)
    {
        state.isLoading = true;
    },
    [tasksMutationsConstants.DELETE_ACTION_START]()
    {
       state.isDeleteActionStart = true;
    },
    [tasksMutationsConstants.PUT_TASK](state,task)
    {
        state.isLoading = false;
        state.tasks.push(task);
    },
    [tasksMutationsConstants.REMOVE_TASK](context, taskId)
    {
        state.isDeleteActionStart = false;
        context.tasks = context.tasks.filter(task => task.id != taskId );
    },
    [tasksMutationsConstants.UPDATE_TASK](context,task)
    {
        context.tasks = context.tasks.map(t => {
            if(t.id != task.id) return t;
            else
                return task;
        })
    },
    [tasksMutationsConstants.TOGGLE_STATUS](context, taskId)
    {
        context.tasks = context.tasks.map(task => {
            if(task.id != taskId) return task;
            else{
                task.completed = !task.completed;
                return task;
            }
        })
    },
    [tasksMutationsConstants.PUT_ACTIVE_CATEGORY](context, taskId)
    {
         if(taskId == context.activeTaskId) return;
         context.activeTaskId = taskId;
    },
    [tasksMutationsConstants.PUT_TASK_ID_FOR_DELETE](context, taskId)
    {
        context.taskIdForDelete  = taskId;
    },
    [tasksMutationsConstants.HIDE_DELETE_DIALOG](context)
    {
        context.taskIdForDelete  = null;
    }
};

const getters = {
    tasks: state => state.tasks,
    isActiveTask: state => id => id == state.activeTaskId,
    activeTaskId: state => state.activeTaskId,
    findActiveIndex: state => id => state.tasks.findIndex(task => task.id == id),
    isLoading: state => state.isLoading,
    needShowDeleteDialog: state => state.taskIdForDelete != null,
    isDeleteActionStart: state => state.isDeleteActionStart
};

export default {
    namespaced: true,
    state,
    actions,
    mutations,
    getters
};