import {appMutationsConstants} from "../constants/applicationConstants";

const initialState = {
    needShowNavigation: false,
    needShowCreateTaskForm: false,
    needShowMediumTasksNavigation: false
}

export const state = Object.assign({},initialState);

export const mutations = {
    [appMutationsConstants.TOGGLE_NAVIGATION](context)
    {
        context.needShowNavigation = !context.needShowNavigation;
    },
    [appMutationsConstants.TOGGLE_CREATE_FORM](context)
    {
         context.needShowCreateTaskForm = !context.needShowCreateTaskForm;
    },
    [appMutationsConstants.TOGGLE_MEDIUM_TASKS_NAVIGATION](context)
    {
        context.needShowMediumTasksNavigation = !context.needShowMediumTasksNavigation;
    }
};

const getters = {
    needShowNavigation: state => state.needShowNavigation,
    needShowCreateTaskForm: state => state.needShowCreateTaskForm,
    needShowMediumTasksNavigation: state => state.needShowMediumTasksNavigation
};

export default {
    namespaced: true,
    state,
    mutations,
    getters
};