<template>
  <div :class="navigationWrapperClass">
      <div class="navigation-wrapper__title">Навигация</div>
      <div class="navigation-wrapper__toggle" @click="toggleNavigation()">
          <i class="material-icons">
              home
          </i>
      </div>
    <ul class="navigation">
        <li v-for="link in links"
            :key="link.text"
        >
            <router-link
                    :to="{'name' : link.name}"
                    class="navigation__link"
                    active-class="navigation__link--active"
                    exact
            >
                {{  link.text }}
            </router-link>
        </li>
    </ul>
  </div>
</template>

<script>
import {mapGetters,mapMutations} from 'vuex';
import {APP_MODULE, appMutationsConstants} from "../constants/applicationConstants";

export default {
  name: 'navigation',
  data: () => ({
      links: [
          {name: "tasks-list", text: "Задания"},
          {name: "register-form", text: "Регистрация"},
          {name: "about-me", text: "О себе"},
      ]
  }),
    computed: {
        ...mapGetters({
            needShowNavigation: `${APP_MODULE}/needShowNavigation`
        }),
        navigationWrapperClass()
        {
            return {
                'navigation-wrapper': true,
                'navigation-wrapper--visible': this.needShowNavigation
            }
        }
    },
    methods: {
        ...mapMutations({
            toggleNavigation: `${APP_MODULE}/${appMutationsConstants.TOGGLE_NAVIGATION}`
        })
    }

}
</script>

