<template>
   <div v-if="needShow">
      <circles-spinner :isLoading="isDeleteActionStart"></circles-spinner>
      <div
              class="delete-task-modal-wrapper">
         <div class="delete-task-modal">
            <div class="delete-task-modal__close"
                 @click="hideDeleteModal()"
            >
               <i class="material-icons">close</i>
            </div>
            <div class="delete-task-modal__title">Вы действительно хотите удалить это задание?</div>
            <div class="delete-task-modal__actions">
               <button class="delete-task-modal__btn delete-task-modal__btn--cancel"
                    @click="hideDeleteModal()"
                    :disabled="isDeleteActionStart"
               >
                  Отмена
               </button>
               <button class="delete-task-modal__btn delete-task-modal__btn--delete"
                    @click="deleteTask()"
                    :disabled="isDeleteActionStart"
               >
                  Удалить
               </button>
            </div>
         </div>
      </div>
   </div>
</template>

<script>
import {mapGetters,mapMutations,mapActions} from 'vuex';
import {TASKS_MODULE, tasksActionsContstants, tasksMutationsConstants} from "../../constants/tasksConstants";
import CirclesSpinner from "../Shared/Spinner/CirclesSpinner.vue";

export default {
    components: {CirclesSpinner},
    name: 'delete-task-modal',
  props: ['needShow'],
  computed: {
      ...mapGetters({
          isDeleteActionStart: `${TASKS_MODULE}/isDeleteActionStart`
      })
  },
  methods: {
      ...mapMutations({
          hideDeleteModal: `${TASKS_MODULE}/${tasksMutationsConstants.HIDE_DELETE_DIALOG}`
      }),
      ...mapActions({
          deleteTask: `${TASKS_MODULE}/${tasksActionsContstants.DELETE_TASK}`
      })
  }
}
</script>

